import {
  deleteServerCard,
  addLikeServerCard,
  deleteLikeServerCard,
} from "./api.js";

import {
  initialCards,
  places,
  placeTemplate,
  confirmDeletePicturePopup,
  confirmDeletePictureButton,
  closePopupConfirmDeletePictureButton,
} from "./constants.js";
import { closePopup, openPopup } from "./modal.js";

//
//

// –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –º–µ—Å—Ç–∞ –≤ –¥–æ–∫—É–º–µ–Ω—Ç HTML (–ø—Ä–∏–Ω–∏–º–∞–µ—Ç id –∫–∞—Ä—Ç–æ—á–∫–∏ –∏ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ª–∞–π–∫–∞)
export function addPlacesInHtml(card_id, likeState) {
  const element = initialCards[initialCards.length - 1]; // –ø—Ä–∏—Å–≤–æ–∏—Ç—å —ç–ª–µ–º–µ–Ω—Ç—É –∏–Ω–¥–µ–∫—Å –ø–æ—Å–ª–µ–¥–Ω–µ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞
  const placeElement = placeTemplate
    .querySelector(".elements__element")
    .cloneNode(true); // –∫–ª–æ–Ω–∏—Ä–æ–≤–∞—Ç—å —à–∞–±–ª–æ–Ω –∫–∞—Ä—Ç–æ—á–∫–∏
  placeElement.querySelector(".elements__element_pic").src = element.link; // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å —à–∞–±–ª–æ–Ω—É —Å—Å—ã–ª–∫—É –Ω–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ
  placeElement.querySelector(".elements__element_header").textContent =
    element.name; // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å —à–∞–±–ª–æ–Ω—É –Ω–∞–∑–≤–∞–Ω–∏–µ –º–µ—Å—Ç–∞
  placeElement.querySelector(".elements__element_like-counter").textContent =
    element.likes; // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å —à–∞–±–ª–æ–Ω—É –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ª–∞–π–∫–æ–≤
  placeElement
    .querySelector(".elements__element_pic")
    .setAttribute("alt", element.name); // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å alt –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—é –≤ —à–∞–±–ª–æ–Ω–µ –∫–∞—Ä—Ç–æ—á–∫–∏
  if (likeState) {
    placeElement
      .querySelector(".elements__element_like-img")
      .classList.add("elements__element_like-img_active");
  } // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –ª–∞–π–∫—É –∞–∫—Ç–∏–≤–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –µ—Å–ª–∏ –µ—Å—Ç—å –∞–∫—Ç–∏–≤–Ω—ã–µ –ª–∞–π–∫–∏
  placeElement
    .querySelector(".elements__element_pic")
    .setAttribute("dataId", card_id); // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—é –≤ —à–∞–±–ª–æ–Ω–µ id –∫–∞—Ä—Ç–æ—á–∫–∏
  places.prepend(placeElement); // –¥–æ–±–∞–≤–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É –≤ –Ω–∞—á–∞–ª–æ —Å–µ–∫—Ü–∏–∏ —Å –∫–∞—Ä—Ç–æ—á–∫–∞–º–∏ –º–µ—Å—Ç

  // –ö–Ω–æ–ø–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è –º–µ—Å—Ç–∞
  const deletePictureButton = document.querySelector(
    ".elements__element_pic_remove-button"
  );

  // C–∫—Ä—ã—Ç—å —Å —á—É–∂–∏—Ö –∫–∞—Ä—Ç–∏–Ω–æ–∫ –∫–Ω–æ–ø–æ–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
  if (!element.userIsOwner) {
    deletePictureButton.classList.add("disable_btn");
  }
}

// –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –º–µ—Å—Ç–∞ –≤ –º–∞—Å—Å–∏–≤ initialCards (–ø—Ä–∏–Ω–∏–º–∞–µ—Ç –∏–º—è, —Å—Å—ã–ª–∫—É –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É, –≤–ª–∞–¥–µ–ª—å—Ü–∞, id –∫–∞—Ä—Ç–æ—á–∫–∏ –∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ª–∞–π–∫–æ–≤)
export function addPlaceInArray(name, link, owner, cardId, likes) {
  let initialCard = {}; // –æ–±—ä—è–≤–ª–µ–Ω–∏–µ –º–∞—Å—Å–∏–≤–∞
  initialCard.name = name; // –∏–º—è
  initialCard.link = link; // —Å—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É
  initialCard.userIsOwner = owner; // –≤–ª–∞–¥–µ–ª—å–µ—Ü
  initialCard.id = cardId; // id –∫–∞—Ä—Ç–æ—á–∫–∏
  initialCard.likes = likes.length; // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ª–∞–π–∫–æ–≤
  initialCards.push(initialCard); // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –≤ –∫–æ–Ω–µ—Ü –º–∞—Å—Å–∏–≤–∞
}

// –£—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ/—Å–Ω—è—Ç–∏–µ –ª–∞–π–∫–∞
export function addLike() {
  const likeButton = document.querySelector(".elements__element_like-img");
  likeButton.addEventListener("click", function (evt) {
    const cardId = likeButton
      .closest(".elements__element")
      .querySelector(".elements__element_pic")
      .getAttribute("dataId");
    const eventTarget = evt.target;
    if (!eventTarget.classList.contains("elements__element_like-img_active")) {
      // –£—Å—Ç–∞–Ω–æ–≤–∫–∞ –ª–∞–π–∫–∞ –º–µ—Å—Ç—É –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ
      (async function () {
        try {
          const likeCount = await addLikeServerCard(cardId, );
          eventTarget
            .closest(".elements__element")
            .querySelector(".elements__element_like-counter").textContent =
            likeCount.likes.length;
        } catch (err) {
          console.error(`–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫: ${err}`);
        }
      })();
    } else {
      // –£–¥–∞–ª–∏—Ç—å –ª–∞–π–∫ –º–µ—Å—Ç–∞ –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ
      (async function () {
        try {
          const likeCount = await deleteLikeServerCard(cardId, );
          eventTarget
            .closest(".elements__element")
            .querySelector(".elements__element_like-counter").textContent =
            likeCount.likes.length;
        } catch (err) {
          console.error(`–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫: ${err}`);
        }
      })();
    }
    evt.target.classList.toggle("elements__element_like-img_active");
  });
}

// –£–¥–∞–ª–µ–Ω–∏–µ –∫–∞—Ä—Ç–æ—á–∫–∏ –º–µ—Å—Ç–∞ –∏–∑ HTML –∏ –º–∞—Å—Å–∏–≤–∞
export function deletePlaceCard(item) {
  const place = item.target.closest(".elements__element");
  place.remove(); // —É–¥–∞–ª–∏—Ç—å –º–µ—Å—Ç–æ –∏–∑ html
  removePlaceFromArray(place, initialCards); // —É–¥–∞–ª–∏—Ç—å –º–µ—Å—Ç–æ –∏–∑ –º–∞—Å—Å–∏–≤–∞
}

// –£–¥–∞–ª–µ–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç–∞ –∏–∑ –º–∞—Å—Å–∏–≤–∞
function removePlaceFromArray(place, initialCards) {
  const placeId = place
    .closest(".elements__element")
    .querySelector(".elements__element_pic"); // –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –±–ª–∏–∂–∞–π—à–µ–≥–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
  let arrayIndex;
  initialCards.findIndex((item, index) => {
    const id = item.id === placeId.getAttribute("dataId");
    if (!id) return;
    if (id) {
      arrayIndex = index;
      // –£–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É —Å —Å–µ—Ä–≤–µ—Ä–∞
      (async function () {
        try {
          await deleteServerCard(item.name, item.link, item.id);
        } catch (err) {
          console.error(`–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫: ${err}`);
        }
      })();
    }
  });
  initialCards.splice(arrayIndex, 1);
}

// –ü–µ—Ä–µ–º–µ–Ω–Ω–∞—è –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è –∫–Ω–æ–ø–æ–∫ "—É–¥–∞–ª–∏—Ç—å –º–µ—Å—Ç–æ"
let deletePictureButtons;

// –£–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫–∏
export function deletePictureButton() {
  // –ó–∞–ø–∏—Å—å –∫–Ω–æ–ø–æ–∫ –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é
  deletePictureButtons = document.querySelectorAll(
    ".elements__element_pic_remove-button"
  );

  // –û—Ç–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–∞ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è –∏ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è –Ω–∞–∂–∞—Ç–∏–µ–º –Ω–∞ –∫–Ω–æ–ø–∫—É
  deletePictureButtons.forEach((button) => {
    button.addEventListener("click", (evt) => {
      const targetDeletePictureButton = evt; // –∑–∞–ø–∏—Å–∞—Ç—å –≤ –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –Ω–∞–∂–∞—Ç—É—é –∫–Ω–æ–ø–∫—É
      openPopup(confirmDeletePicturePopup); // –æ—Ç–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è

      confirmDeletePictureButton.addEventListener("click", (evt) => {
        evt.preventDefault();
        (async function () {
          try {
            await deletePlaceCard(targetDeletePictureButton); // —É–¥–∞–ª–∏—Ç—å –∫–∞—Ä—Ç–æ—á–∫—É —Å —Å–µ—Ä–≤–µ—Ä–∞
          } catch (err) {
            console.error(`–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫: ${err}`);
          }
        })();
        closePopup(confirmDeletePicturePopup); // –∑–∞–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è
      }); // –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–Ω–æ–ø–∫–∏ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è ("–î–∞")
    }); // –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏ ("–ö–æ—Ä–∑–∏–Ω–æ—á–∫–∏")
  }); // —É—Å—Ç–∞–Ω–æ–≤–∫–∞ —Å–ª—É—à–∞—Ç–µ–ª—è –Ω–∞ –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–µ–∫
}

// –ó–∞–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è –Ω–∞–∂–∞—Ç–∏–µ–º –Ω–∞ –∫–Ω–æ–ø–∫—É –∑–∞–∫—Ä—ã—Ç–∏—è –ø–æ–ø–∞–ø–∞
closePopupConfirmDeletePictureButton.addEventListener("click", (evt) => {
  evt.preventDefault();
  closePopup(confirmDeletePicturePopup); // –∑–∞–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è
}); // –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –∫–Ω–æ–ø–∫–∏ –∑–∞–∫—Ä—ã—Ç–∏—è –ø–æ–ø–∞–ø–∞ –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–µ–Ω–∏—è —É–¥–∞–ª–µ–Ω–∏—è –∫–∞—Ä—Ç–æ—á–∫–∏
