import { placeValidInfoClear } from "./validate.js";

import {
  avatarInput,
  popupOpened,
  popupClosed,
  userNameField,
  profileUserName,
  profileUserAbout,
  userAboutField,
  input,
  placeNameField,
  placeLinkField,
  fullScreenPicturePopup,
  overlayHeader,
  profileUserAvatar,
} from "./constants.js";

// ---- –£–ü–†–ê–í–õ–ï–ù–ò–ï –ü–û–ü–ê–ü–û–ú ----
//
// –û—Ç–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–∞
export function openPopup(button) {
  button.classList.add(popupOpened); // –¥–æ–±–∞–≤–∏—Ç—å –∫–ª–∞—Å—Å –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ –ø–æ–ø–∞–ø–∞
  button.classList.remove(popupClosed); // —É–¥–∞–ª–∏—Ç—å –∫–ª–∞—Å—Å –∑–∞–∫—Ä—ã—Ç–æ–≥–æ –ø–æ–ø–∞–ø–∞
}

// –ó–∞–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–∞
export function closePopup(button) {
  button.classList.remove(popupOpened); // —É–¥–∞–ª–∏—Ç—å –∫–ª–∞—Å—Å–∞ –æ—Ç–∫—Ä—ã—Ç–æ–≥–æ –ø–æ–ø–∞–ø–∞
  button.classList.add(popupClosed); // –¥–æ–±–∞–≤–∏—Ç—å –∫–ª–∞—Å—Å–∞ –∑–∞–∫—Ä—ã—Ç–æ–≥–æ –ø–æ–ø–∞–ø–∞
  getProfileInfo(); // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∞–∫—Ç—É–∞–ª—å–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –≤ –ø–æ–ª—è –≤–≤–æ–¥–∞ –ø—Ä–æ—Ñ–∏–ª—è
  placeFieldsClear(); // –æ—á–∏—Å—Ç–∫–∞ –ø–æ–ª–µ–π –º–µ—Å—Ç–∞ –≤ –ø–æ–ø–∞–ø–µ –∑–∞–≥—Ä—É–∑–∫–∏ –º–µ—Å—Ç–∞
  placeValidInfoClear(); // –æ—á–∏—Å—Ç–∫–∞ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ–± –æ—à–∏–±–∫–µ
}

// –ó–∞–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø –∫–ª–∏–∫–æ–º –Ω–∞ –æ–≤–µ—Ä–ª–µ–π
export function closePopupOnClick(popup) {
  popup.forEach((item) => {
    item.addEventListener("mousedown", function (evt) {
      if (evt.target.classList.contains("popup") && evt.button === 0) {
        closePopup(this.closest(".popup"));
      }
    });
  });
}

// –ó–∞–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø –∫–ª–∞–≤–∏—à–µ–π ESC
export function closePopupOnEsc(popup) {
  document.addEventListener("keyup", function (evt) {
    popup.forEach((item) => {
      if (!item.classList.contains("popup_closed") && evt.key === "Escape") {
        closePopup(item);
      }
    });
  });
}

// ---- –£–ü–†–ê–í–õ–ï–ù–ò–ï –û–¢–û–ë–†–ê–ñ–ï–ù–ò–Ø –¢–ï–ö–°–¢–ê ----
//
// –£—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –∑–Ω–∞—á–µ–Ω–∏–π –∏–º–µ–Ω–∏ –∏ –æ–ø–∏—Å–∞–Ω–∏—è –≤ –ø–æ–ª—è –≤–≤–æ–¥–∞ –ø–æ–ø–∞–ø–∞ –ø—Ä–æ—Ñ–∏–ª—è
export function getProfileInfo() {
  userNameField.value = profileUserName.textContent.trim(); // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ –∏–º–µ–Ω–∏
  userAboutField.value = profileUserAbout.textContent.trim(); // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ –æ–ø–∏—Å–∞–Ω–∏—è
}

// –û—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –ø—Ä–æ—Ñ–∏–ª—è –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –∏ —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∏–µ –∑–Ω–∞—á–µ–Ω–∏–π –≤ –ø–æ–ª—è –≤–≤–æ–¥–∞
export function saveProfileInfo() {
  profileUserName.textContent = userNameField.value.trim(); // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ –∏–º–µ–Ω–∏
  profileUserAbout.textContent = userAboutField.value.trim(); // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ –æ–ø–∏—Å–∞–Ω–∏—è
  getProfileInfo(); // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏—è –≤ –∏–Ω–ø—É—Ç—ã
}

// –û—á–∏—Å—Ç–∫–∞ –ø–æ–ª–µ–π –≤–≤–æ–¥–∞ –º–µ—Å—Ç–∞ (–ù–∞–∑–≤–Ω–∏–µ –º–µ—Å—Ç–∞ –∏ —Å—Å—ã–ª–∫–∞)
export function placeFieldsClear() {
  placeNameField.value = "";
  placeLinkField.value = "";
}

// –°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –∏ –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–∞ –ø—Ä–∏ –Ω–∞–∂–∞—Ç–∏–∏ –Ω–∞ Enter
export function saveFormInfoOnEnter() {
  input.forEach((item) => {
    item.addEventListener("keyup", function (evt) {
      if (
        evt.code === "Enter" &&
        evt.target.matches(".profile-input" && evt.target.button)
      ) {
        saveProfileInfo(); // —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –ø—Ä–æ—Ñ–∏–ª—è
        closePopup(this.closest(".popup")); // –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–∞
      }
      if (
        evt.code === "Enter" &&
        evt.target.matches(".place-input") &&
        evt.target.button
      ) {
        saveAddedPicture(); // —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ –º–µ—Å—Ç–∞
        closePopup(this.closest(".popup")); // –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–∞
      }
    });
  });
}

// –û—Ç–∫—Ä—ã—Ç–∏–µ –ø–æ–ø–∞–ø–∞ —Å –∫–∞—Ä—Ç–∏–Ω–∫–æ–∏ÃÜ
export function popupOverlay() {
  const pictures = document.querySelectorAll(".elements__element_pic");
  pictures.forEach((picture) => {
    picture.addEventListener("click", function (evt) {
      fullScreenPicturePopup.querySelector(".full-screen-picture").src =
        evt.target.src;
      overlayHeader.textContent = evt.target
        .closest(".elements__element")
        .querySelector(".elements__element_header").textContent;
      openPopup(fullScreenPicturePopup);
    });
  });
}

// –°–æ—Ö—Ä–∞–Ω–∏—Ç—å –∏ –∑–∞–∫—Ä—ã—Ç—å –ø–æ–ø–∞–ø —Å –∞–≤–∞—Ç–∞—Ä–æ–º
export function uploadAvatar() {
  profileUserAvatar.src = avatarInput.value; // —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å —Å—Å—ã–ª–∫—É –∏–∑ –∏–Ω–ø—É—Ç–∞ –≤ html –¥–æ–∫—É–º–µ–Ω—Ç
}

// –ò–∑–º–µ–Ω–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –∫–Ω–æ–ø–∫–∏ –≤–æ –≤—Ä–µ–º—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è
export function savingButtonText(button) {
  button.textContent = "–°–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ...";
}

// –í–æ–∑–≤—Ä–∞—Ç —Ç–µ–∫—Å—Ç–∞ –∫–Ω–æ–ø–∫–∏ –ø–æ—Å–ª–µ —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è
export function savedButtonText(button) {
  button.textContent = "–°–æ—Ö—Ä–∞–Ω–∏—Ç—å";
}
